
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import { getYMDHMFormateTime, isArrayType } from "../../../js/utility.js"
import { mapState } from 'vuex'

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: 'news',
components: {
Group,
Badge,
Cell
},
data() {
return {}
},
computed: {
...mapState({
newsDetail: state => state.newsDetail,
userInfo: state => state.userInfo
}),
contentHeight() {
return window.innerHeight - 46;
},
receive_time() {
return getYMDHMFormateTime(this.newsDetail.receive_time);
},
announcement_time() {
return getYMDHMFormateTime(this.newsDetail.announcement_time);
},
remind_time() {
return getYMDHMFormateTime(this.newsDetail.remind_time);
},
},
mounted() {
if (this.newsDetail.length > 0) {
this.$refs.my_scroller.finishInfinite(true);
this.getSilentInfo();
}
},
methods: {
infinite(done) {
AjaxSilentHttp('POST', 'm=message&a=index', { by: 'dynamic' }, this, function(res, wself, success) {
if (success) {
done(true);
wself.$store.commit('updateNewsDetail', wself.getShowArray(res.data));
}
});
},
refresh(done) {
AjaxSilentHttp('POST', 'm=message&a=index', { by: 'dynamic' }, this, function(res, wself, success) {
if (success) {
done(true);
wself.$store.commit('updateNewsDetail', wself.getShowArray(res.data));
}
});
},

getSilentInfo() {
AjaxSilentHttp('POST', 'm=message&a=index', { by: 'dynamic' }, this, function(res, wself, success) {
if (success) {
wself.$store.commit('updateNewsDetail', wself.getShowArray(res.data));
}
});
},
getShowArray(data) {
var tempArray = [];
tempArray.push({ title: '企业公告', markImage: require('../../../assets/notice.png'), detail: data.new_announcement_info, time: getYMDHMFormateTime(data.announcement_time), num: data.announcement_noread_num, showNum: data.announcement_noread_num !== '0', type: '0' });
tempArray.push({ title: '小助手', markImage: require('../../../assets/zhushou.png'), detail: data.new_receive_info, time: getYMDHMFormateTime(data.receive_time), num: data.receive_noread_num, showNum: data.receive_noread_num !== '0', type: '1' });
tempArray.push({ title: '提醒助手', markImage: require('../../../assets/tx.png'), detail: data.remind_info, time: getYMDHMFormateTime(data.remind_time), num: data.remind_noread_num, showNum: data.remind_noread_num !== '0', type: '2' });

return tempArray;
},
enterAssistant(type) {
if (type === '0') {
this.$router.push({
name: 'notice'
})
} else {
this.$router.push({
name: 'littleassistant',
params: {
type: type 
}
})
}

}
}
}




